/**
 * @class Asignatura
 * Clase que crea las asignaturas y contiene las calificaciones de los estudiantes.
 * > ---
 * > ### Atributos:
 * > - `#nombre`: Nombre para las asignaturas. [String]
 * > - `#calificaciones`: Mapa que contendr√° el ID el Estudiante y sus calificaciones. [Object]
 * >
 * > ### Constructor:
 * > - Lo primero que hago es verificar si el nombre es v√°lido (solo letras y espacios).
 * >     + Si es v√°lido, le asigno el nombre al atributo "this.#nombre" y el atributo "this.#calificacion" lo inicializo a un mapa vac√≠o.
 * >     + Si no es v√°lido, mostrar√° un error indicando que el nombre no es v√°lido.
 * >
 * > ### Getter:
 * > - Declaro el getter de nombre, que me devuelve el nombre de la Asignatura.
 * >
 * > ### M√©todos:
 * > - `obtenerCalificaciones(id_Estudiante)`: Devuelvo un array con las calificaciones del Estudiante que paso por par√°metros, de una asignatura determinada.
 * > - `addCalificacion(id_Estudiante, calificacion)`: A√±ade una nota al Estudiante en una asignatura determinada. Pide por par√°metros el id_Estudiante y la calificaci√≥n a a√±adir.
 * >     + 1¬∫ - Comprueba que la calificaci√≥n es correcta (un n√∫mero y, >0 y <10). Si no es correcta, lanza un error.
 * >     + 2¬∫ - Comprueba si "this.#calificaciones" ya tiene registrado el ID del Estudiante.
 * >         - Si tiene dentro ya una Key que es la del Id del Estudiante, le a√±ade otra calificaci√≥n.
 * >         - Si no, a√±ade de un nuevo id_Estudiante y una calificaci√≥n a "this.#calificacion".
 * >> ( Compruebo con "mapa.has.(key)", a√±ado calificaci√≥n con "mapa.get(key).push(valor)" y a√±ado uno nuevo con "mapa.set(key, [valor])").
 * > - `calcularPromedioGeneral()`: Calcula el promedio de las calificaciones que hay en una Asignatura. Para hacerlo hago lo siguiente:
 * >     + 1¬∫ - Declaro dos variables:
 * >         - "total_Notas": contendr√° la suma total de todas las notas. [Number]
 * >         - "cantidad_Notas":  contendr√° el n√∫mero total de notas. [Number]
 * >     + 2¬∫ - Luego recorro "this.#calificaciones" (usando un .forEach).
 * >     + 3¬∫ - Dentro de este creo:
 * >         - "suma_Notas": variable que sumar√° todas las notas (usando .reduce).
 * >         - "total_Notas": sumar√° todas las suma_Notas con la iteraci√≥n del bucle.
 * >         - "contidad_Notas": sumar√° la cantidad de notas que tiene cada Estudiante.
 * >    + 4¬∫ - Compruebo si hay alguna calificaci√≥n (total_Notas > 0):
 * >        - Si no, devuelvo "Sin calificaciones".
 * >       - Si tenia, devuelvo el c√°lculo del promedio (total_Notas / cantidad_Notas), con dos decimales (usando .toFixed(2) -> [String]).
 * > - `calcularPromedioEstudiante(id_Estudiante)`: Calcula el promedio de las calificaciones de un Estudiante en una Asignatura. Para hacerlo hago lo siguiente:
 * >     + 1¬∫ - Declaro:
 * >         - "notas": usa el m√©todo "obtenerCalificaciones(id_Estudiante)" para obtener las calificaciones.
 * >         - "total_Notas": sumar√° todas las notas.
 * >         - "cantidad_Notas": obtendr√° la cantidad de notas.
 * >    + 2¬∫ - Y por √∫ltimo, devuelvo lo mismo que en el m√©todo anterior.
 * > - `mostrarAsignaturas()`: Sirve para mostrar todas las Asignaturas de una forma formateada.
 * > ---
 */
export class Asignatura{

    // Atributos privados
    #nombre;
    #calificaciones;

    /**
     * Constructor de la clase Asignatura que crea una instancia de esta.
     * 
     * @param {string} nombre - Nombre de la Asignatura.
     * @throws {Error} Si el nombre no esta formado solo por letras y espacios.
     * 
     * @example
     * // Crear una instancia de Asignatura.
     * const asignatura = new Asignatura("DAWES");
     */
    constructor(nombre) {

        if ((/^[a-zA-Z√°√©√≠√≥√∫√Å√â√ç√ì√ö ]+$/).test(nombre)) {
            this.#nombre = nombre;
            this.#calificaciones = new Map();
        }else{
            throw new Error("El nombre de la Asignatura es erroneo");
        }
    }

    /**
     * Obtiene el nombre de la Asignatura.
     * 
     * @returns {string} Nombre de la Asignatura.
     */
    get nombre() {
        return this.#nombre;
    }
    
    // Metodos de la clase Asignatura.
    /**
     * @function
     * @description Obtener calificaciones de un Estudiante en una Asignatura.
     * 
     * @param {string} id_Estudiante - ID del Estudiante.
     * @returns {number[]} Array con las calificaciones del Estudiante en una Asignatura.
     * 
     * @example
     * // Devuelve un array con las calificaciones del Estudiante.
     * // Por ejemplo: [5, 10, 7, 8]
     */
    obtenerCalificaciones(id_Estudiante) {
        return this.#calificaciones.get(id_Estudiante) || [];
    }
    
    /**
     * @function
     * @description A√±ade una calificaci√≥n para un Estudiante en una Asignatura.
     * 
     * @param {string} id_Estudiante - ID del Estudiante.
     * @param {number} calificacion - Calificaci√≥n a a√±adir.
     * 
     * @throws {Error} Si la calificaci√≥n no es un n√∫mero o es NaN.
     * @throws {Error} Si la calificaci√≥n no es v√°lida (0 a 10).
     */
    addCalificacion(id_Estudiante, calificacion) {

        if (typeof calificacion !== "number" || isNaN(calificacion)) {
            throw new Error(`La calificaci√≥n asignada a la asignatura ${this.nombre} no es un numero`);
        }

        if (calificacion < 0 || calificacion > 10) {
            throw new Error(`La calificac√≥n asignada a la asignatura ${this.#nombre} no es valida (0 a 10)`);
        }
         
        if (this.#calificaciones.has(id_Estudiante)) {

            this.#calificaciones.get(id_Estudiante).push(calificacion);
        }else{

            this.#calificaciones.set(id_Estudiante, [calificacion]);
        }
    }

    /**
     * @function
     * @description Calcula el promedio general de las calificaciones totales, de una Asignatura.
     * 
     * @returns {string | number} Promedio General o sin calificaciones.
     * 
     * @example
     * // Devuelve el promedio general de las calificaciones o, en su defecto, "sin calificaciones" .
     * // Por ejemplo: 7.50
     */
    calcularPromedioGeneral() {

        let total_Notas = 0;
        let cantidad_Notas = 0;
    
        this.#calificaciones.forEach((notas, id) => {

            let suma_Notas = 0;
            suma_Notas = notas.reduce((total, valorAct) => total + valorAct, 0);
            total_Notas += suma_Notas;

            cantidad_Notas += notas.length;
        });

        return total_Notas > 0 ? (total_Notas / cantidad_Notas).toFixed(2) : "Sin calificaciones"; 
    }

    /**
     * @function
     * @description Calcula el promedio de las calificaciones de un Estudiante en una Asignatura.
     * 
     * @param {string} id_Estudiante - ID del Estudiante. 
     * 
     * @returns {string|number} Promedio del Estudiante en una Asignatura.
     * 
     * @example
     * // Devuelve el promedio de las calificaciones de un Estudiante en una Asignatura o, en su defecto, "sin evaluar".
     * // Por ejemplo: 7.50
     */
    calcularPromedioEstudiante(id_Estudiante) {

        const notas = this.obtenerCalificaciones(id_Estudiante);
        const total_Notas = notas.reduce((total, valorAct) => total + valorAct, 0);
        const cantidad_Notas = notas.length; 

        return total_Notas > 0 ? (total_Notas / cantidad_Notas).toFixed(2) : "Sin evaluar";
    }

    /**
     * @function
     * @description Muestra una representaci√≥n de los datos formateados de las Asignaturas y sus calificaciones.
     * 
     * @returns {string} Cadena con los datos de las Asignaturas formateados.
     * 
     * @example
     * // Devuelve una cadena con los datos de las Asignaturas formateados.
     * // Por ejemplo:
     * // ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ
     * //            DAWES
     * // ===============================
     * // - DAWES: 5 - 10 - 7 - 8
     * // ------------------------------
     * //   - Promedio = 7.50
     */
    mostrarAsignaturas() {
        let mostrar_Asig = "‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ \n" +
                           `            ${this.#nombre} \n` +
                           "=============================== \n";
        if (this.#calificaciones.size === 0) {
            mostrar_Asig += "Sin calificaciones \n" +
                            "============================== \n";  
        }else {
            this.#calificaciones.forEach((notas, id) => {
                mostrar_Asig += `${id}: ${notas.join(" - ")} \n`;
            });
            mostrar_Asig += "------------------------------ \n" +
                            `  - Promedio = ${this.calcularPromedioGeneral()} \n` +
                            "‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ \n";   
        }

        return mostrar_Asig;
    }
}