/**
 * FUNCIONES DEL MEN√ö
 * ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ
 * Aqui creo las funciones principales que son la estructura principal del proyecto. Se crean diferentes men√∫s en los que se pueden realizar operaciones relacionadas con Estudiantes y Asignaturas.
 * 
 * > ---
 * > ### Funciones Principales:
 * > - `menuPrincipal()`: Punto de entrada principal que permite seleccionar una acci√≥n en relaci√≥n con los Estudiantes y las Asignaturas. Funcionalidad: 
 * >    + Genero un bucle que finalizara cuando se indique con una opci√≥n del men√∫.
 * >    + Muestro el men√∫. Ejemplo:
 * >> "Men√∫ Principal:
 * >> 1. Agregar Estudiante / Asignatura
 * >> 2. Eliminar Estudiante / Asignatura
 * >> 3. Mostrar Estudiantes / Asignaturas
 * >> 4. Matricular Estudiante
 * >> 5. Desmatricular Estudiante
 * >> 6. A√±adir Calificacion
 * >> 7. Mostrar Historial de un Estudiante
 * >> 8. Buscar Estudiante / Asignatura
 * >> 0. Salir del programa"
 * >    + Pido una opci√≥n con "prompt" y est√° la uso en un "switch". En funci√≥n de la opci√≥n que se seleccione har√° una cosa u otra.
 * > - `men√∫Secundario()`: Men√∫ que permite seleccionar Estudiantes o Asignaturas. Usando en las opciones de "1. Agregar Estudiante / Asignatura" o "2. Eliminar Estudiante / Asignatura", entre otras. Ejemplo:
 * >> "Seleccione una opcion: 
 * >> 1. Estudiantes 
 * >> 2. Asignaturas 
 * >> 0. Salir"
 * >
 * > ### Funciones Auxiliares:
 * > - `seleccionarEstudiante()`: Muestra una lista numerada de los Estudiantes registrados, permitiendo seleccionar uno para realizar operaciones con √©l. Usado en "2. Eliminar Estudiante / Asignatura", por ejemplo.
 * > - `seleccionarAsignatura()`: Muestra una lista numerada de las Asignaturas registradas, permitiendo seleccionar una para realizar operaciones con ella. Usado en "2. Eliminar Estudiante / Asignatura", por ejemplo.
 * > - `mostrarAsignaturasMatricula(posicion_Est)`: Con el valor que se pasa por par√°metros se obtiene el alumno. De esta manera, permite ver todas las Asignaturas que est√°n registradas y al lado en cuales el alumno est√° matriculado. Usado en "4. Matricular Estudiante".
 * > - `mostrarAsignaturasEstudiante(posicion_Est)`: Con el valor que se pasa por par√°metros se obtiene el alumno. De esta manera, permite mostrar solo las asignaturas en las que se encuentra matriculado este. Usado en "5. Desmatricular Estudiante".
 * >
 * > ### L√≥gica de Iteracci√≥n:
 * > El funcionamiento del proyecto es el siguiente:
 * > - Se muestra el men√∫ principal por consola y se piede con un "prompt" que se introduzca una opci√≥n. Una vez seleccionada se seleccionara en el "switch" la opci√≥n indicada, ejecutando los procesos necesarios para realizar el proceso seleccionado.
 * > - Cuando se halla completado el proceso se volvera al men√∫ principal, de esta manera se podr√° seleccionar otra opci√≥n o se podr√° salir del programa indicando la opci√≥n de salida.
 * >
 * > ### Notas:
 * > - Todo el sistema usa "console.log" para mostrar datos, "prompt" para recoger datos de entrada y "console.clear()" para limpiar la consola (para que queda algo mejor).
 * > - Se tiene en cuenta que el usuario, en los men√∫s, seleccione una opci√≥n no valida, indicando a trav√©s de un "alert()" que ha introducido una opci√≥n no v√°lida.
 * > ---
 */

/**
 * @function
 * @description Muestra el men√∫ secundario y recoge la opci√≥n indicada.
 * 
 * @returns {string} La opci√≥n indicada.
 * 
 * @example
 * // Devolvera lo que el usuario introduzca.
 * // Por ejemplo: 2
 */
export function menuSecundario() {
    console.log("Seleccione una opcion: \n" +
                "1. Estudiantes \n" +
                "2. Asignaturas \n" +
                "0. Salir"
    );

    return prompt("Seleccione una opcion: ");
}

/**
 * @function
 * @description Muestra el men√∫ de Estudiantes y recoge la opci√≥n indicada.
 * 
 * @returns {string|number} La opci√≥n indicada.
 * 
 * @example
 * // Devolvera lo que el usuario introduzca.
 * // Por ejemplo: 2
 */
export function seleccionarEstudiante() {
    let lista_Estudiantes = "";
    let contador = 1;

    lista.estudiantes.forEach(alumno => { 
        lista_Estudiantes += `${contador} - [${alumno.id}] ${alumno.nombre}\n`;
        contador++; 
    });

    console.log("Seleccione un Estudiante:\n" +
                `${lista_Estudiantes}`+
                "0 - Salir");
    
    return prompt("Seleccione una opcion: ") || 0;
}

/**
 * @function
 * @description Muestra el men√∫ de Asignaturas y recoge la opci√≥n indicada.
 * 
 * @returns {string|number} La opci√≥n indicada.
 * 
 * @example
 * // Devolvera lo que el usuario introduzca.
 * // Por ejemplo: 2
 */
export function seleccionarAsignatura() {
    let lista_Asignaturas = "";
    let contador = 1;

    lista.asignaturas.forEach(materia => { 
        lista_Asignaturas += `${contador} - ${materia.nombre}\n`;
        contador++; 
    });

    console.log("Seleccione una Asignatura:\n" +
                `${lista_Asignaturas}`+
                "0 - Salir");
    
    return prompt("Seleccione una opcion: ") || 0;
}

/**
 * @function
 * @description Muestra todas las Asignaturas registradas e indica en cuales est√° matriculado el Estudiante.
 * 
 * @param {number} posicion_Est - √çndice del Estudiante en "lista.estudiantes". 
 * 
 * @returns {string|number} La opci√≥n indicada.
 * 
 * @example
 * // Devolvera lo que el usuario introduzca.
 * // Por ejemplo: 2
 */
export function mostrarAsignaturasMatricula(posicion_Est) {
    
    // Compruebo que la posici√≥n sea positiva y no sea mayor a la cantidad de Estudiantes que hay en Lista.
    if (posicion_Est > -1 && posicion_Est < lista.estudiantes.length) {
        const alumno = lista.estudiantes.at(posicion_Est);

        let lista_Asignaturas = `Asignaturas para el Estudiante ${alumno.nombre}: \n`;
        let contador = 1;

        lista.asignaturas.forEach(materia => { 
            const matriculacion = alumno.asignaturas.includes(materia) ? "[Matriculado]" : "";
            lista_Asignaturas += `${contador} - ${materia.nombre} ${matriculacion}\n`;
            contador++;
        });

        console.log(` ${lista_Asignaturas}` +
                    "0 - Salir");
        
        return prompt("Seleccione una opcion: ") || 0;
    }else {
        return lista.asignaturas.length + 2; // Devuelvo la cantidad de Estudiantes + 2.
    }
}

/**
 * @function
 * @description Muestra todas las Asignaturas en las que est√° matriculado el Estudiante.
 * 
 * @param {number} posicion_Est - √çndice del Estudiante en "lista.estudiantes".
 * 
 * @returns {string|number} La opci√≥n indicada.
 * 
 * @example
 * // Devolvera lo que el usuario introduzca.
 * // Por ejemplo: 2
 */
export function mostrarAsignaturasEstudiante(posicion_Est) {

    if (posicion_Est > -1 && posicion_Est < lista.estudiantes.length) {
        const alumno = lista.estudiantes.at(posicion_Est);

        let lista_Asignaturas = `Asignaturas para el Estudiante ${alumno.nombre}: \n`;
        let contador = 1;

        lista.asignaturas.forEach(materia => { 
            if (alumno.asignaturas.includes(materia)) {

                const notas = materia.obtenerCalificaciones(alumno.id);
                lista_Asignaturas += `${contador} - ${materia.nombre} [${notas.join(" - ")}] \n`;
                contador++;
            }
        });

        console.log(` ${lista_Asignaturas}` +
                    "0 - Salir");
        
        const opcion = prompt("Seleccione una opcion: "); 
        return opcion < contador ? opcion : lista.asignaturas.length + 2;
    }else {
        return lista.asignaturas.length + 2;
    }
}